/* BUTTON CSS */

:root {
  --text-color: hsla(210, 50%, 85%, 1);
  --btn-color: hsl(330, 100%, 71%);
  --bg-color: #ff69b4;
}

button {
  position:relative;
  padding: 10px 20px;  
  border: none;
  cursor: pointer;
  width: 400px;
  font-family: "Source Code Pro";
  font-weight: 900;
  text-transform: uppercase;
  font-size: 20px;  
  color: hsla(210, 50%, 85%, 1);  
  color: var(--text-color);
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: hsl(330, 100%, 71%);
  background-color: var(--btn-color);
  box-shadow: var(--shadow-color) 2px 2px 22px;
  border-radius: 4px; 
  z-index: 0;  
  overflow: hidden;   
}

button:focus {
  outline-color: transparent;
  box-shadow: hsl(330, 100%, 71%) 2px 2px 22px;
  box-shadow: var(--btn-color) 2px 2px 22px;
}

.right::after, button::after {
  content: var(--content);
  display: block;
  position: absolute;
  white-space: nowrap;
  padding: 40px 40px;
  pointer-events:none;
}

.right, .left {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

.right::after {
  top: -30px;
  left: calc(-66% - 20px);
  
  background-color: #ff69b4;
  
  background-color: var(--bg-color);
  color:transparent;
  transition: transform .2s ease-out;
  transform: translate(0, -90%) rotate(0deg)
}

button:hover .right::after {
  transform: translate(0, -47%) rotate(0deg)
}

button .right:hover::after {
  transform: translate(0, -50%) rotate(-7deg)
}

button .left:hover ~ .right::after {
  transform: translate(0, -50%) rotate(7deg)
}

/* bubbles */
button::before {
  content: '';
  pointer-events: none;
  opacity: .6;
  background:
    radial-gradient(circle at 20% 35%,  transparent 0,  transparent 2px, hsla(210, 50%, 85%, 1) 3px, hsla(210, 50%, 85%, 1) 4px, transparent 4px),
    radial-gradient(circle at 35% 44%, transparent 0,  transparent 2px, hsla(210, 50%, 85%, 1) 3px, hsla(210, 50%, 85%, 1) 4px, transparent 4px),
    radial-gradient(circle at 46% 52%, transparent 0, transparent 4px, hsla(210, 50%, 85%, 1) 5px, hsla(210, 50%, 85%, 1) 6px, transparent 6px),
    radial-gradient(circle at 26% 12%, transparent 0, transparent 4px, hsla(210, 50%, 85%, 1) 5px, hsla(210, 50%, 85%, 1) 6px, transparent 6px),
    radial-gradient(circle at 6% -2%, transparent 0, transparent 4px, hsla(210, 50%, 85%, 1) 5px, hsla(210, 50%, 85%, 1) 6px, transparent 6px),
    radial-gradient(circle at -30% -10%, transparent 0, transparent 4px, hsla(210, 50%, 85%, 1) 5px, hsla(210, 50%, 85%, 1) 6px, transparent 6px),
    radial-gradient(circle at -60% -20%, transparent 0, transparent 4px, hsla(210, 50%, 85%, 1) 5px, hsla(210, 50%, 85%, 1) 6px, transparent 6px);
  background:
    radial-gradient(circle at 20% 35%,  transparent 0,  transparent 2px, var(--text-color) 3px, var(--text-color) 4px, transparent 4px),
    radial-gradient(circle at 35% 44%, transparent 0,  transparent 2px, var(--text-color) 3px, var(--text-color) 4px, transparent 4px),
    radial-gradient(circle at 46% 52%, transparent 0, transparent 4px, var(--text-color) 5px, var(--text-color) 6px, transparent 6px),
    radial-gradient(circle at 26% 12%, transparent 0, transparent 4px, var(--text-color) 5px, var(--text-color) 6px, transparent 6px),
    radial-gradient(circle at 6% -2%, transparent 0, transparent 4px, var(--text-color) 5px, var(--text-color) 6px, transparent 6px),
    radial-gradient(circle at -30% -10%, transparent 0, transparent 4px, var(--text-color) 5px, var(--text-color) 6px, transparent 6px),
    radial-gradient(circle at -60% -20%, transparent 0, transparent 4px, var(--text-color) 5px, var(--text-color) 6px, transparent 6px);

  width: 200%;
  height: 400%;
  top: 0;
  left: 0;
  position: absolute;
  -webkit-animation: bubbles 5s linear infinite both;
          animation: bubbles 5s linear infinite both;
}

@-webkit-keyframes bubbles {
  from {
    transform: translate();
  }
  to {
    transform: translate(0, -60%);
  }
}

@keyframes bubbles {
  from {
    transform: translate();
  }
  to {
    transform: translate(0, -60%);
  }
}
/*END OF BUTTON CSS */
